.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 5vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

div.componentdiv {
  border-width:3px;  
  border-style: inset;
  border-color: blueviolet;
  width: 50%

}

#bg {
  position: fixed; 
  top: 0; 
  left: 0; 
	right: 0;
  /* Preserve aspet ratio */
  min-width: 100%;
  min-height: 100%;
}