div.header Link {
    float: left;
    color: black;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 18px; 
    line-height: 25px;
    border-radius: 4px;
    background-color: whitesmoke;
    border: solid;
  }

  div.header {
    position: fixed; 
    top:0%;
    right: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0,0,0,0.65);
    padding: 20px 10px;
    font-family: Verdana, Geneva, sans-serif;
    z-index: 1;
    color: white;
    opacity: 0.50;
  }

  div.header-right {
    float: right;
  }

div.header Link.home_active {
    background-color: dodgerblue;
    color: white;
  }

  div.header Link {
    color: white;
   }

   h6 {
     color: white;
   }